
.index {
  width: 25%;
  height: 100%;
  float: left;
  padding-bottom: 4.4rem;
}
.index > .select {
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0.8rem 2.5rem;
    clear: both;
    overflow: hidden;
}
.index > .select > ul {
      margin: 0;
      padding: 1.5rem 0;
      list-style: none;
      clear: both;
      overflow: hidden;
      margin-top: 1rem;
}
.index > .select > ul > li:nth-child(1) {
        float: left;
}
.index > .select > ul > li:nth-child(1) > li {
          maring: 0;
          float: left;
}
.index > .select > ul > li:nth-child(2) {
        float: right;
}
.index > .select > ul > li > ul {
        list-style: none;
        padding: 0;
        maring: 0;
}
.index > .select > ul > li > ul > li {
          width: 7rem;
          height: 3.2rem;
          background: url("./img/border.png") no-repeat;
          background-size: 100%;
          padding: 1rem;
}
.index > .select > ul > li > ul > li > a {
            text-decoration: none;
            color: #222222;
}
.index > .banner {
    width: 100%;
    height: 11rem;
    background: red;
}
.swiper-container {
  height: 13rem !important;
}
.swiper-box {
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
.swiper-item {
  height: 100%;
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.swiper-pagination-bullet-active {
  background: #ceac52 !important;
}
.swiper-pagination-bullet {
  border-radius: 0;
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}
.swiper-container {
  height: 12rem !important;
}



