
.familyName {
  overflow: hidden;
}
.nameImg {
  width: 100%;
  height: 12rem;
  background: red;
}
.content {
  width: 100%;
  position: relative;
  top: 0;
  overflow-y: hidden;
}
.content > .masking {
    width: 100%;
    height: 100%;
    position: fixed;
    background: #999999;
    opacity: 0.5;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    z-index: 10;
}
.cf {
  width: 15.3rem;
  height: 50rem;
  background: #fff;
  position: absolute;
  right: 0;
  z-index: 10;
  top: 16rem;
  background: red;
}
.rightNav {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  width: 15.3rem;
  background: #fff;
  position: absolute;
  right: -100%;
  z-index: 11;
  top: 16rem;
  -webkit-box-shadow: 0 1px 5px #000000;
          box-shadow: 0 1px 5px #000000;
}
.rightNav > ul {
    width: 100%;
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    list-style: none;
    padding: 0;
}
.rightNav > ul > li {
      width: 100%;
      padding: 1rem;
      text-align: left;
}



