
.bottomNav {
  position: fixed;
  width: 100%;
  height: 4.4rem;
  background: #fff;
  bottom: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.bottomNav > ul {
    width: 100%;
    margin: 0 !important !important;
    list-style: none;
    padding: 0;
    height: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.bottomNav > ul > li {
      width: 25%;
      float: left;
      height: 3.6666666rem;
      text-align: center;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      padding: 0.8333333rem 2.3333333rem;
      text-align: center;
      position: relative;
}
.bottomNav > ul > li > img {
        width: 1.5rem;
        height: 2rem;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
}
.bottomNav > ul > li > p {
        display: inline-block;
        font-size: 10px;
        position: absolute;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
        bottom: -7px;
}
.bottomNav > ul > li > .ace {
        color: #c1ad54;
}



